.faq__item {
  position: relative;
  padding: 42px 0 37px 0;
}

.faq__item::before,
.faq__item::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  background-color: var(--icon-bg-color);
}

.faq__item::before {
  top: 0;
}

.faq__item::after {
  bottom: -1px;
}
