:root {
  --color-black: #000;
  --color-grey-dark: #414141;
  --color-grey: #666;
  --color-grey-light: #cacaca;
  --color-white: #fff;
  --color-orange: #f90;
  --color-orange-dark: #e1670e;
  --color-orange-light: #ffb84d;
  --color-transparent: transparent;
  --text-color: var(--color-black);
  --text-light-color: var(--color-white);
  --text-footer-color: var(--color-grey-light);
  --text-placeholder-color: var(--color-grey-light);
  --bg-color: var(--color-white);
  --bg-footer-color: var(--color-black);
  --input-bg-color: var(--color-grey-dark);
  --input-focus-bg-color: var(--color-grey);
  --icon-bg-color: var(--color-grey-light);
  --link-hover-color: var(--color-orange);
  --link-active-color: var(--color-orange-dark);
  --button-bg-color: var(--color-orange);
  --button-hover-bg-color: var(--color-orange-light);
  --button-active-bg-color: var(--color-orange-dark);
  --button-transparent-bg-color: var(--color-transparent);
  --button-transparent-hover-bg-color: var(--color-orange);
  --button-grey-hover-bg-color: var(--color-grey);
  --button-grey-active-bg-color: var(--color-black);
  --font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url(/fonts/Montserrat-Regular.woff2) format("woff2"), url(/fonts/Montserrat-Regular.woff) format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url(/fonts/Montserrat-Medium.woff2) format("woff2"), url(/fonts/Montserrat-Medium.woff) format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url(/fonts/Montserrat-SemiBold.woff2) format("woff2"), url(/fonts/Montserrat-SemiBold.woff) format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url(/fonts/Montserrat-Bold.woff2) format("woff2"), url(/fonts/Montserrat-Bold.woff) format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  src: url(/fonts/Montserrat-ExtraBold.woff2) format("woff2"), url(/fonts/Montserrat-ExtraBold.woff) format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.page {
  max-width: 1920px;
  min-width: 320px;
  margin: 0 auto;
  font-family: var(--font-family);
  color: var(--text-color);
  background-color: var(--bg-color);
}

.page__header {
  margin-bottom: 50px;
}

.header {
  display: flex;
  align-items: center;
  padding: 14px 75px 14px 69px;
  border-radius: 0px 0px 32px 32px;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 1680px) {
  .header {
    justify-content: space-between;
  }
}
@media screen and (max-width: 1200px) {
  .header__logo {
    order: 2;
  }
}

.logo {
  display: block;
  cursor: pointer;
}

.logo--place--header {
  max-width: 225px;
}

.logo__img {
  display: block;
  width: 100%;
}

.header__menu {
  margin-left: auto;
}

@media screen and (max-width: 1680px) {
  .header__menu {
    margin-left: 0;
  }
}
.menu {
  font-weight: 600;
  font-size: 18px;
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.menu__list--place--header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 76px;
}
@media screen and (max-width: 1200px) {
  .menu__list--place--header {
    position: absolute;
    top: 0;
    left: 0;
    flex-direction: column;
    min-height: 80vh;
    background-color: var(--bg-color);
  }
}

.menu__toggle {
  box-sizing: content-box;
  display: none;
  width: 30px;
  height: 23px;
  padding: 3px;
  background-color: transparent;
  border: 2px solid transparent;
  outline: none;
}
.menu__toggle:hover {
  border-color: var(--link-hover-color);
}
.menu__toggle:focus {
  border-color: var(--link-hover-color);
}
.menu__toggle:active .menu__toggle-icon {
  fill: var(--link-active-color);
}
@media screen and (max-width: 1200px) {
  .menu__toggle {
    display: block;
  }
}

.menu__link {
  display: block;
  box-sizing: border-box;
  text-decoration: none;
  cursor: pointer;
}

.menu__link--place--header {
  font-size: 18px;
  line-height: 1.22;
  padding: 5px;
  color: var(--text-color);
}

.menu__link--place--header:hover {
  border-bottom: 2px solid var(--link-hover-color);
}

.menu__link--place--header:focus {
  border: 2px solid var(--link-hover-color);
  outline: none;
}

.menu__link--place--header:active {
  color: var(--link-active-color);
  border-bottom: 2px solid var(--link-active-color);
}

.header__search {
  margin-left: 250px;
}
@media screen and (max-width: 1680px) {
  .header__search {
    margin-left: 0;
  }
}
@media screen and (max-width: 1200px) {
  .header__search {
    order: 3;
  }
}

.search__open-button {
  box-sizing: border-box;
  padding: 5px;
  margin-right: -5px;
  border: none;
  background-color: var(--color-transparent);
  cursor: pointer;
  outline: none;
}

.search__open-button:hover .search__open-icon rect {
  fill: var(--link-hover-color);
}

.search__open-button:hover .search__open-icon path {
  stroke: var(--link-hover-color);
}

.search__open-button:focus {
  border: 2px solid var(--link-hover-color);
}

.search__open-button:focus .search__open-icon rect {
  fill: var(--text-color);
}

.search__open-button:focus .search__open-icon path {
  stroke: var(--text-color);
}

.search__open-button:active .search__open-icon rect {
  fill: var(--link-hover-color);
}

.search__open-button:active .search__open-icon path {
  stroke: var(--link-hover-color);
}

.search__open-icon {
  display: block;
  width: 24px;
  height: 24px;
}

.search__form {
  position: absolute;
  box-sizing: border-box;
  width: 539px;
  display: none;
  align-items: center;
  padding: 16px 13px 16px 32px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.12);
  border-radius: 24px;
}

.search__input {
  box-sizing: border-box;
  width: 412px;
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 10px;
  padding-bottom: 5px;
  font-size: 18px;
  line-height: 1;
  color: var(--text-color);
  border: none;
  border-bottom: 1px solid var(--text-placeholder-color);
  outline: none;
}

.search__input::placeholder {
  font-size: 18px;
  line-height: 1;
  font-weight: 500;
  color: var(--text-placeholder-color);
}

.search__submit {
  width: 24px;
  height: 24px;
  background-color: var(--color-transparent);
  outline: none;
  border: none;
  cursor: pointer;
}

.content {
  max-width: 1770px;
  padding: 0 75px;
}

.content__hero {
  margin-bottom: 50px;
}

.hero {
  position: relative;
  padding: 102px 80px 100px 80px;
  background-color: #8ec5e7;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 32px;
}

.hero--bg--1 {
  background-image: url(../../../images/hero-1_1920.jpg);
}

.hero__title {
  max-width: 837px;
  margin: 0;
  margin-bottom: 24px;
  font-size: 70px;
  line-height: 1.21;
  font-weight: 800;
  color: var(--text-light-color);
}

.heading-text {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
}

.heading-text--color--white {
  color: var(--text-light-color);
}

.heading-text--color--black {
  color: var(--text-color);
}

.hero__text {
  max-width: 790px;
  margin: 0;
  margin-bottom: 90px;
}

.button {
  box-sizing: border-box;
  text-align: center;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  outline: none;
}

.button--size--m {
  padding: 25px 39px;
}

.button--size--l {
  padding: 19px 52px;
}

.button--size--s {
  padding: 20px 38px;
}

.button--size--xs {
  padding: 17px 35px;
}

.button--bg--color {
  background-color: var(--button-bg-color);
}

.button--bg--color:hover,
.button--bg--color:focus {
  background-color: var(--button-hover-bg-color);
}

.button--bg--color:active {
  background-color: var(--button-active-bg-color);
}

.button--bg--transparent {
  background-color: var(--button-transparent-bg-color);
  border: 3px solid var(--button-bg-color);
}

.button--bg--transparent:hover,
.button--bg--transparent:focus {
  background-color: var(--button-bg-color);
}

.button--bg--transparent:active {
  border-color: var(--button-active-bg-color);
  background-color: var(--button-active-bg-color);
}

.button--text--black {
  padding: 19px 37px;
  font-size: 16px;
  line-height: 1.25;
  color: var(--text-color);
  border: 1px solid var(--text-color);
  background-color: var(--button-transparent-bg-color);
}

.button--text--black:hover,
.button--text--black:focus {
  color: var(--text-light-color);
  border-color: var(--button-grey-hover-bg-color);
  background-color: var(--button-grey-hover-bg-color);
}

.button--text--black:active {
  color: var(--text-light-color);
  border-color: var(--button-grey-active-bg-color);
  background-color: var(--button-grey-active-bg-color);
}

.button--text--white {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  color: var(--text-light-color);
}

.button--text--big {
  font-size: 23px;
  line-height: 1.22;
  font-weight: 700;
  color: var(--text-light-color);
}

.hero__slider-dots {
  position: absolute;
  bottom: 30px;
  left: 50%;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  transform: translateX(-50%);
}

.hero__slider-dot {
  width: 15px;
  height: 15px;
  background-color: var(--icon-bg-color);
  border-radius: 50%;
  cursor: pointer;
}

.hero__slider-dot--active {
  background-color: var(--bg-color);
}

.content__about {
  margin-bottom: 100px;
}

.title {
  margin: 0;
  font-weight: normal;
  font-size: 48px;
  line-height: 1.23;
}

.title--color--black {
  color: var(--text-color);
}

.title--color--cacaca {
  color: var(--text-footer-color);
}

.about__title {
  margin-bottom: 20px;
}

.about__description {
  max-width: 1235px;
  margin: 0;
  margin-bottom: 50px;
}

.text {
  font-size: 16px;
  line-height: 2;
}

.text--color--black {
  color: var(--text-color);
}

.text--color--white {
  color: var(--text-light-color);
}

.text--weight--normal {
  font-weight: normal;
}

.text--weight--700 {
  font-weight: 700;
}

.about__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(597px, 1fr));
  gap: 33px;
}

.about__info-card {
  position: relative;
  min-height: 622px;
  background-color: #89675a;
  background-image: url(../../../images/about_1920.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 32px;
}

.about__info-content {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 32px;
}

.about__text {
  max-width: 433px;
  margin: 0;
  margin-right: 30px;
}

.about__advantages-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style-type: none;
}

.about__advantages-item {
  position: relative;
  padding: 65px 65px 61px 296px;
  border: 1px solid var(--icon-bg-color);
  border-radius: 32px;
}

.about__advantages-item::before {
  position: absolute;
  content: "";
  top: 62px;
  left: 54px;
  width: 190px;
  height: 165px;
  background-repeat: no-repeat;
  background-position: 0 0;
}

.about__advantages-title {
  margin: 0;
  margin-bottom: 8px;
}

.about__advantages-text {
  margin: 0;
  max-width: 450px;
}

.about__advantages-item--position--first {
  margin-bottom: 32px;
}

.about__advantages-item--position--first::before {
  background-image: url(../../../../images/about-2.svg);
}

.about__advantages-item--position--second::before {
  background-image: url(../../../../images/about-1.svg);
}

.content__order {
  margin-bottom: 109px;
}

.order {
  display: grid;
  grid-template-areas: "title title" "list list" "text picture" "buttons picture";
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
}

.order__title {
  margin-bottom: 44px;
  grid-area: title;
}

.order__list {
  display: flex;
  grid-area: list;
  gap: 76px;
  margin: 0;
  margin-bottom: 50px;
  margin-left: -5px;
  padding: 0;
  list-style-type: none;
}

.order__number {
  padding: 5px;
  font-size: 18px;
  line-height: 1.22;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}

.order__number:hover {
  color: var(--link-hover-color);
}

.order__number:focus {
  padding: 3px;
  color: var(--text-color);
  border: 2px solid var(--button-bg-color);
}

.order__number:active {
  padding: 5px;
  color: var(--button-active-bg-color);
  border: none;
}

.order__number--active {
  color: var(--button-active-bg-color);
}

.order__text-content {
  grid-area: text;
  margin-bottom: 18px;
}

.order__buttons {
  grid-area: buttons;
  display: flex;
  gap: 32px;
}

.order__image-content {
  grid-area: picture;
  display: flex;
  align-items: stretch;
  border-radius: 32px;
  overflow: hidden;
}

.order__img {
  display: block;
  max-width: 100%;
  object-fit: cover;
  height: 100%;
}

.order__heading {
  margin-top: -8px;
  margin-bottom: 8px;
  line-height: 1.33;
}

.order__description {
  margin: 0;
  max-width: 760px;
}

.content__faq {
  margin-bottom: 100px;
}

.faq__title {
  margin: 0;
  margin-bottom: 32px;
}

.faq__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.faq__item {
  position: relative;
  padding: 42px 0 37px 0;
}

.faq__item::before,
.faq__item::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--icon-bg-color);
}

.faq__item::before {
  top: 0;
}

.faq__item::after {
  bottom: -1px;
}

.faq__item--opened .faq__answer {
  display: block;
}

